import React, { Component } from "react";
import "./MainInfoLocationMapText.scss";

class MainInfoLocationMapText extends Component {
  render() {
    return (
      <section className="mainInfoLocationMapText">
        <div>
          <p>
            Nasz apartament to idealne miejsce dla wszystkich, ktrzy ceni
            sobie wygod i wietn lokalizacj. Znajduje si on w sercu miasta,
            zaledwie kilka krokw od urokliwej tni, ktra zapewnia doskonae
            warunki do relaksu i odprenia. Spacerujc po okolicy, natrafisz na
            malownicze parki, idealne na spacery i pikniki, oraz urokliw aleje
            Mickiewicza, ktra zachca do wieczornych przechadzek. Ponadto,
            liczne sklepy i restauracje s tu obok, co sprawia, e wszystko,
            czego potrzebujesz, znajduje si w zasigu rki. Nasz apartament to
            prawdziwy raj dla tych, ktrzy ceni sobie dostp do atrakcji i
            udogodnie, jednoczenie zachowujc spokj i cisz miejsca.
            Zapraszamy do naszego wyjtkowego zaktka!
          </p>
        </div>
      </section>
    );
  }
}

export default MainInfoLocationMapText;
