import React, { Component } from "react";
import "./MainInfoApartmentTitle.scss";
import { Fade } from "react-awesome-reveal";

class MainInfoApartmentTitle extends Component {
  render() {
    return (
      <Fade
        className="mainInfoApartmentTitleContainer"
        direction="up"
        triggerOnce
      >
        <div className="mainInfoApartmentTitle">
          <p>Apartamenty Uchnasta</p>
          <p>
            Zapraszamy do naszego pensjonatu w Busku-Zdroju, synnym
            uzdrowiskowym miecie pooonym w malowniczym otoczeniu. Oferujemy
            komfortowy wypoczynek i relaks w pobliu synnej buskiej tni
            solankowej.
          </p>
        </div>
      </Fade>
    );
  }
}

export default MainInfoApartmentTitle;
